.dataObjects {
  align-self: stretch;
  position: relative;
}
.discoverTheWorld {
  width: 100%;
  position: relative;
  font-size: 14px;
  font-family: var(--font-rubik);
  display: inline-block;
  max-width: 700px;
}
.dataObjectsParent,
.frameParent {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-3xs);
}
.frameParent {
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: center;
  gap: var(--gap-11xl);
}
.iphone1415ProMax4 {
  position: relative;
  width: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding: 50px;
  box-sizing: border-box;
  gap: var(--gap-11xl);
  background-image: url(/public/iphone-14--15-pro-max--4@3x.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
  text-align: center;
  font-size: 40px;
  color: var(--color-white);
  font-family: var(--font-share-tech-mono);
}
