@import url("https://fonts.googleapis.com/css2?family=Share+Tech+Mono:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Rubik:wght@400&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-rubik: Rubik;
  --font-share-tech-mono: "Share Tech Mono";

  /* font sizes */
  --font-size-xs: 12px;
  --font-size-9xl: 28px;
  --font-size-base: 16px;

  /* Colors */
  --color-gray: rgba(255, 255, 255, 0.1);
  --color-white: #fff;

  /* Gaps */
  --gap-11xl: 30px;
  --gap-3xs: 10px;

  /* Paddings */
  --padding-xl: 20px;
  --padding-8xs: 5px;
  --padding-3xs: 10px;

  /* Border radiuses */
  --br-3xs: 10px;
  --br-8xs: 5px;
}
