.theObelisco {
  position: relative;
}
.thermometerOfInequality {
  align-self: stretch;
  position: relative;
  font-size: var(--font-size-base);
  font-family: var(--font-rubik);
}
.theObeliscoParent {
  align-self: stretch;
  flex-direction: column;
  align-items: flex-start;
}
.frameGroup,
.readMoreWrapper,
.theObeliscoParent {
  display: flex;
  justify-content: center;
}
.readMoreWrapper {
  border-radius: var(--br-8xs);
  background-color: var(--color-gray);
  border: 1px solid var(--color-white);
  flex-direction: row;
  align-items: center;
  padding: var(--padding-8xs) var(--padding-3xs);
  cursor: pointer;
  font-size: var(--font-size-xs);
  font-family: var(--font-rubik);
}
.frameGroup {
  flex: 1;
  flex-direction: column;
  align-items: flex-start;
  gap: var(--gap-3xs);
  z-index: 0;
}
.obelisco2Icon,
.obelisco3Icon {
  position: absolute;
  margin: 0 !important;
  top: 0;
  right: 0;
  height: 198px;
  object-fit: cover;
}
.obelisco3Icon {
  width: 228.7px;
  opacity: 0.3;
  mix-blend-mode: lighten;
  z-index: 1;
}
.obelisco2Icon {
  width: 206px;
  z-index: 2;
}
.frameParent {
  flex: 1;
  border-radius: var(--br-3xs);
  height: 198px;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding: var(--padding-xl);
  box-sizing: border-box;
  position: relative;
  gap: var(--gap-3xs);
  background-image: url(/public/frame-2@3x.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
  min-width: 330px;
  max-width: 420px;
  cursor: pointer;
  text-align: left;
  font-size: var(--font-size-9xl);
  color: var(--color-white);
  font-family: var(--font-share-tech-mono);
}
